.Preview {
    background: rgba(100, 100, 100, 0.2);
    border: 10px solid rgba(0, 0, 0, 0);
    border-radius: 10px;
    width: 165px;
    height: 165px;
    margin-bottom: 15px;
}

@media only screen and (max-width: 1600px) {
    .Preview {
        border-radius: 5px;
    }

}


.Preview-board {
    display: grid;
    grid-gap: 2px;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(4, 1fr);
    width: 150px;
    max-width: 150px;
    height: 150px;
    max-height: 150px;
}

@media only screen and (max-width: 2400px) {
        .Preview {
            border: 5px solid rgba(0, 0, 0, 0);
            width: 82.5px;
            height: 82.5px;
        }
        
        .Preview-board {
            grid-gap: 1px;
            height: 75px;
            width: 75px;
        }

}

@media only screen and (max-width: 1600px) {
    .Preview {
        border: 3.3px solid rgba(0, 0, 0, 0);
        width: 55px;
        height: 55px;
    }
    
    .Preview-board {
        grid-gap: 0.6px;
        height: 50px;
        width: 50px;
    }

}